import React from "react";
import ImageGallery from "react-image-gallery";
import Popup from "reactjs-popup";
import { useEffect } from "react";
import { useState } from "react";
import MovingText from "react-moving-text";
import "./css/Portfolio.css";
import $, { event } from "jquery";
import { useRef } from "react";
const Portfolio = () => {
  const [playanimate, updateanimate] = useState("");
  const imgpokemon = [
    {
      original: "../img/Pokemon-search/img1.PNG ",
      originalClass: "p1",
    },
    {
      original: "../img/Pokemon-search/img2.PNG",
      originalClass: "p1",
    },
    {
      original: "../img/Pokemon-search/img3.png",
      originalClass: "p1",
    },
  ];
  const imgbangkok = [
    {
      original: "../img/Bangkokian-journey/img1.PNG",
      originalClass: "p2",
    },
    {
      original: "../img/Bangkokian-journey/img2.PNG",
      originalClass: "p2",
    },
    {
      original: "../img/Bangkokian-journey/img3.png",
      originalClass: "p2",
    },
  ];
  const imgmai = [
    {
      original: "../img/Mai-Mai/img1.PNG",
    },
    {
      original: "../img/Mai-Mai/img2.PNG",
    },
    {
      original: "../img/Mai-Mai/img3.png",
    },
  ];
  const imgsevenwonder = [
    {
      original: "../img/7Wonder/img1.PNG",
    },
    {
      original: "../img/7Wonder/img2.PNG",
    },
    {
      original: "../img/7Wonder/img3.png",
    },
  ];
  const imgprotobot = [
    {
      original: "../img/Protobot/img1.PNG",
    },
    {
      original: "../img/Protobot/img2.PNG",
    },
    {
      original: "../img/Protobot/img3.png",
    },
  ];
  const playRefp1 = useRef(null);
  const playRefp2 = useRef(null);
  const playRefp3 = useRef(null);
  const playRefp4 = useRef(null);
  const playRefp5 = useRef(null);

  useEffect(() => {
    $("#Next").on("click", function (e) {
      let right = $("#containerproject").scrollLeft() + 315;
      $("#containerproject").animate({ scrollLeft: right }, 800);
    });
    $("#Pre").on("click", function (e) {
      let left = $("#containerproject").scrollLeft() - 315;
      $("#containerproject").animate({ scrollLeft: left }, 800);
    });
  });

  let screen768 = window.matchMedia("(max-width: 768px)");
  function myFunction(screen768) {
    window.addEventListener("scroll", (event) => {
      const displayport = document.getElementById("portpage");
      if (screen768.matches) {
        console.log(window.scrollY);
        if (window.scrollY >= 2731 && window.scrollY <= 3000) {
          displayport.style.color = "blue";
        } else {
          displayport.style.color = "rgb(213, 224, 228)";
        }
      } else {
        if (window.scrollY >= 1636 && window.scrollY <= 2279) {
          displayport.style.color = "blue";
        } else {
          displayport.style.color = "rgb(213, 224, 228)";
        }
      }
    });
  }
  myFunction(screen768);
  screen768.addListener(myFunction);

  return (
    <div className="Portfoloi">
      <div className="container" id="port-effect">
        <h2>Portfoloi</h2>
        <div className="projectlist" id="containerproject">
          <div className="container-in">
            <div
              className="boxject"
              id="p1"
              onMouseOver={() => {
                playRefp1.current["play"]();
              }}
              onMouseOut={() => {
                playRefp1.current["pause"]();
              }}
            >
              <div className="img">
                <ImageGallery
                  items={imgpokemon}
                  ref={playRefp1}
                  showFullscreenButton={false}
                  showPlayButton={false}
                  showNav={false}
                  slideDuration={1500}
                  slideInterval={100}
                  disableKeyDown={true}
                />
              </div>

              <p>
                this is website about search Pokemon it can show each pokemon
                information data whether it's name, Weight, Height, Type and
                Ability
                <h3>
                  <a
                    target="_blank"
                    href="https://pokemon-search-data.netlify.app"
                  >
                    ViewProject
                  </a>
                </h3>
                <div className="logoskill">
                  <img src="../img/skill/js.png" alt="" />
                  <img src="../img/skill/css.png" alt="" />
                  <img src="../img/skill/html.png" alt="" />
                  <img src="../img/skill/react.png" alt="" />
                </div>
              </p>
            </div>
          </div>

          <div className="container-in">
            <div
              className="boxject"
              id="p2"
              onMouseOver={() => {
                playRefp2.current["play"]();
              }}
              onMouseOut={() => {
                playRefp2.current["pause"]();
              }}
            >
              <div className="img">
                <ImageGallery
                  items={imgbangkok}
                  ref={playRefp2}
                  showFullscreenButton={false}
                  showPlayButton={false}
                  showNav={false}
                  slideDuration={1500}
                  slideInterval={100}
                  disableKeyDown={true}
                />
              </div>

              <p>
                this is website about social depravity Public Transportation in
                bankok such as bus, BTS, MAT, boat, taxi, motorbike taxi
                <h3>
                  <a
                    target="_blank"
                    href="https://bangkokia-jouney.netlify.app"
                  >
                    ViewProject
                  </a>
                </h3>
                <div className="logoskill">
                  <img src="../img/skill/js.png" alt="" />
                  <img src="../img/skill/css.png" alt="" />
                  <img src="../img/skill/html.png" alt="" />
                  <img src="../img/skill/bootstrap.png" alt="" />
                </div>
              </p>
            </div>
          </div>

          <div className="container-in">
            <div
              className="boxject"
              id="p3"
              onMouseOver={() => {
                playRefp3.current["play"]();
              }}
              onMouseOut={() => {
                playRefp3.current["pause"]();
              }}
            >
              <div className="img">
                <ImageGallery
                  items={imgmai}
                  ref={playRefp3}
                  showFullscreenButton={false}
                  showPlayButton={false}
                  showNav={false}
                  slideDuration={1500}
                  slideInterval={100}
                  disableKeyDown={true}
                />
              </div>

              <p>
                This is a website for the exchange of cryptocurrencies with a
                manual system. A user can create orders to sell or buy
                cryptocurrencies.
                <h3>
                  <a target="_blank" href="https://maimai-p2p-site.netlify.app">
                    ViewProject
                  </a>
                </h3>
                <div className="logoskill">
                  <img src="../img/skill/js.png" alt="" />
                  <img src="../img/skill/css.png" alt="" />
                  <img src="../img/skill/html.png" alt="" />
                  <img src="../img/skill/bootstrap.png" alt="" />
                  <img src="../img/skill/firebase.png" alt="" />
                </div>
              </p>
            </div>
          </div>

          <div className="container-in">
            <div
              className="boxject"
              id="p4"
              onMouseOver={() => {
                playRefp4.current["play"]();
              }}
              onMouseOut={() => {
                playRefp4.current["pause"]();
              }}
            >
              <div className="img">
                <ImageGallery
                  items={imgsevenwonder}
                  ref={playRefp4}
                  showFullscreenButton={false}
                  showPlayButton={false}
                  showNav={false}
                  slideDuration={1500}
                  slideInterval={100}
                  disableKeyDown={true}
                />
              </div>

              <p>
                This is a website explaining the history of the Seven Wonders of
                the World in three eras. Within the web page, it describes in
                the form of a sheet where each one was built to make it easy to
                understand.
                <h3>
                  <a target="_blank" href="https://seven-wonder.netlify.app">
                    ViewProject
                  </a>
                </h3>
                <div className="logoskill">
                  <img src="../img/skill/js.png" alt="" />
                  <img src="../img/skill/css.png" alt="" />
                  <img src="../img/skill/html.png" alt="" />
                  <img src="../img/skill/bootstrap.png" alt="" />
                </div>
              </p>
            </div>
          </div>

          <div className="container-in">
            <div
              className="boxject"
              id="p5"
              onMouseOver={() => {
                playRefp5.current["play"]();
              }}
              onMouseOut={() => {
                playRefp5.current["pause"]();
              }}
            >
              <div className="img">
                <ImageGallery
                  items={imgprotobot}
                  ref={playRefp5}
                  showFullscreenButton={false}
                  showPlayButton={false}
                  showNav={false}
                  slideDuration={1500}
                  slideInterval={100}
                  disableKeyDown={true}
                />
              </div>

              <p>
                Protobot: An Educational Game for Algorithmic Thinking is a
                webgl that about basic programming by use blockcode to control
                bot to run from start to finish
                <h3>
                  <a
                    target="_blank"
                    href="https://alskdjfhg0.itch.io/webgl-protobot"
                  >
                    ViewProject
                  </a>
                </h3>
                <div className="logoskill">
                  <img src="../img/skill/unity.png" alt="" />
                  <img src="../img/skill/c.png" alt="" />
                </div>
              </p>
            </div>
          </div>
        </div>
        <i class="fa-solid fa-arrow-right" id="Next"></i>
        <i class="fa-solid fa-arrow-left" id="Pre"></i>
      </div>
    </div>
  );
};
export default Portfolio;
